import { Flex, Grid, Image, SimpleGrid, Stack, Text } from "@chakra-ui/react";
import React, { useState } from "react";
import "../styles/Home.css";
import { Icon } from "@iconify/react";

export default function Home() {
  const linkedIn = "https://www.linkedin.com/in/meshiv5/";
  const gmail = "mailto:000done9@gmail.com";
  const leetcode = "https://leetcode.com/Sh1vishere/";
  const github = "https://github.com/meshiv5";
  return (
    <SimpleGrid alignItems={"center"} columns={[1, 1, 2, 2]} w={["80%", "80%", "80%", "50%"]} m="auto" mt={10}>
      <Stack>
        <Text fontSize={50}>Shivendra</Text>
        <Text w={["100%", "100%", "80%", "95%"]} className="desc fontSegoe">
          A passionate individual who always thrive to work on end to end products which develop sustainable and scalable social and technical systems
          to create impact.
        </Text>
        <Flex pt={15} justifyContent="space-between" w={"200px"} alignItems={"center"}>
          <Icon
            onClick={() => {
              window.open(linkedIn, "_blank", "noopener,noreferrer");
            }}
            className="icons"
            icon="entypo-social:linkedin-with-circle"
            color="#0E76A8"
            width={30}
          />

          <Icon
            onClick={() => {
              window.open(leetcode, "_blank", "noopener,noreferrer");
            }}
            className="icons"
            icon="cib:leetcode"
            color="#0E76A8"
            width={30}
          />
          <Icon
            onClick={() => {
              window.open(github, "_blank", "noopener,noreferrer");
            }}
            className="icons"
            icon="codicon:github-inverted"
            color="blue"
            width={30}
          />
          <Icon
            onClick={() => {
              window.open(gmail, "_blank", "noopener,noreferrer");
            }}
            className="icons"
            icon="akar-icons:google-contained-fill"
            width={30}
            color="red"
          />
        </Flex>
        <div id="wrap">
          <a href="https://drive.google.com/file/d/1TznVlav9jnqu28gRzEhq3IvLlWa042in/view?usp=sharing" class="btn-slide" target={"_blank"}>
            <span class="circle">
              <i class="fa fa-rocket"></i>
            </span>
            <span class="title">Check Resume</span>
            <span class="title-hover">Click here</span>
          </a>
        </div>
      </Stack>

      <Flex justifyContent={"center"} mt={[50, 50, 0, 0]}>
        <Image w={400} src={require("../images/FeelingProud.png")} />
      </Flex>
    </SimpleGrid>
  );
}
